<template>
   <div class="modal-start">
      <div @click="closeDialog" class="close-button">
        <v-icon>mdi-close</v-icon>
      </div>
        <v-card class="custom-card">
         <div class="welcome-message">
            <p class="welcome-text"></p>
            <p class="app-name"><span class="accent-color">OQplanner</span> </p>
            <p class="invite-text"> ?</p>
        </div>
        <v-form ref="form" lazy-validation>
          <v-text-field 
            label=" " 
            v-model="tripPlanName" 
            placeholder="   ."
            :rules="tripPlanNameRules"
            class="name-input no-cursor">
          </v-text-field>
          <v-text-field 
            label=" " 
            v-model="tripUserName" 
            placeholder="    ."
            :rules="tripUserNameRules"
            class="name-input no-cursor">
          </v-text-field>
          <!-- <v-text-field 
            label=" " 
            v-model="companionName" 
            :rules="CompanionNameRules"
            placeholder="    ." 
            class="no-cursor"></v-text-field> -->
        </v-form>
        <v-card-actions class="button-container">
          <!--     -->
          <v-btn @click="submitCompanion" color="black" class="action-button"></v-btn>
          <v-btn @click="submitTrip(this.flag)" color="black" class="action-button"></v-btn>
        </v-card-actions>
          <v-alert 
          v-model="alert"
          type="warning"
          class="disabled-alert"
          transition="scale-transition"
          closable
          prominent
          >
          {{ alertMessage }}
        </v-alert>
      </v-card>
    </div>
</template>
<script>
export default {
  name: "TripSelectStart",
  data(){
    return {
        inputs: {},
        tripPlanName: "", //  
        tripUserName: "", //   
        companionName: "", //   
        flag: true,
        tripPlanNameRules: [
          v => !!v || '   .',
          v => !( v && v.length > 8) || '  8    .'
        ],
        tripUserNameRules: [
          v => !!v || '   .',
          v => /^[-a-zA-Z\s]*$/.test(v) || '  /  .',
          v => !( v && v.length > 15) || '  8    .',
        ],
        CompanionNameRules:[
          v => !!v || '   .',
          v => /^[-a-zA-Z\s]*$/.test(v) || '  /  .',
          v => !( v && v.length > 15) || '  8    .',
          // v => this.checkCompanion(v)
        ],
        alertMessage: "", // v-alert  
        alert: false,
    }
  },
  methods: {
   
    closeDialog() {
      this.$emit("closeDialog");
    },
    submitCompanion(){

    },
    async submitTrip(flag){
      await this.$refs.form.validate().then(result => {
          if (result.valid) {
            // const tripPlannerNmEncoded = encodeURIComponent(this.tripUserName);
            // const tripPlanNmEncoded = encodeURIComponent(this.tripPlanName);

            // const tripPlan ={
            //   tripPlannerNm: tripPlannerNmEncoded,
            //   tripPlanNm: tripPlanNmEncoded,
            // }

            const tripPlan ={
              tripPlannerNm: this.tripUserName,
              tripPlanNm: this.tripPlanName,
            }

            // console.log("tripPlannerNm", tripPlannerNmEncoded);
            // console.log("tripPlanNm", tripPlanNmEncoded);

            this.$axios.get('/trip/plan/info', { 
              params: tripPlan 
            }).then(planResponse => {
                  const tripPlanNo = planResponse.data.tripPlanNo;

                  if(tripPlanNo == undefined){
                  console.log("planRes", planResponse.data.tripPlanNo);
                  //   flags.startFlag = true;
                  //   flags.overlay = true;

                  this.inputs.tripPlanName = this.tripPlanName;
                  this.inputs.tripUserName = this.tripUserName;
                  

                  // submitTrip   
                  // TripSelect.vue @submitTrip=submitTrip 
                  this.$emit("submitTrip", flag);
                  this.emitter.emit('submitDetail', this.inputs);
                  
                  // this.$refs.form.reset();
                }else{
                  this.alertMessage = "    .";
                  this.alert = true;
                }
            })
            .catch(error => {
              //      
              console.error(error);
            });
            
          }else{
            console.log('validate fail');
          }
        })
        

   
       
    

    },
  }
};
</script>

<style scoped>
.modal-start {
  background-color: white;
  padding: 20px;
  position: relative;
  width: 20%;
}


.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  z-index: 2; /*      */
}

.close-button v-icon {
  font-size: 24px; /*    */
  width: 24px; /*    */
  height: 24px; /*    */
}

.no-cursor {
  caret-color: transparent; /*     */
}

.custom-card {
  margin-top: 20px;
  position: relative;
}

.welcome-message{
  margin-top: 10px;
  margin-bottom: 10px;
  position: relative;
  max-width: 500px; /*    (  ) */
  width: 100%; /*  100%     */
}

.welcome-text {
  text-align: center;
  font-size: 15px; /*    */
  color: #333; /*    */
}

.app-name {
  text-align: center;
}

.accent-color {
  font-weight: bold;
  color: #333; /*    */
}

.invite-text {
  text-align: center;
  color: #333; /*    */
}

.v-card-actions {
  display: flex;
  justify-content: center;
}

.disabled-alert{
  margin: auto;
  text-align: center;
  width: 100%; 
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.button-container {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.action-button {
  flex: 1;
  margin: 0 10px;
  color: #fff;
}

.action-button.primary {
  background-color: #333;
}

</style>
